body {
  font-family: 'Ubuntu', sans-serif;
}
h1 {
  margin: 0;
  font-family: "Libre Baskerville", serif;
  font-weight: bold;
  font-size: 51px;
  line-height: 74px;
  color: #1F3F68;
}
h2 {
  font-family: Ubuntu;
  font-weight: 500;
  font-size: 36px;
  line-height: 46px;
  color: #316099;
}
h3 {
  font-family: Ubuntu;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #1F3F68;
}
p {
  font-family: Ubuntu;
  font-size: 16px;
  line-height: 26px;
  color: #1F3F68;
  opacity: 0.6;
}
ul {
  padding-left: 0;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
}
.container {
  display: flex;
  max-width: 940px;
  margin: 0 auto;
}

.header {
  position: relative;
  overflow: visible;
  padding-top: 40px;
  background: linear-gradient(0deg, rgba(152, 195, 232, .2) 8.84%, rgba(152, 195, 232, 0) 31.12%);
  min-height: 768px;
  &__top-menu {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 48px;
  }
  &__logo {
    width: 60px;
    height: 48px;
  }
  &__menu-img {
    width: 24px;
    height: 16px;
  }
  &__text-block {
    margin-top: 101px;
    width: 60%;
    p {
      margin-top: 32px;
      padding-right: 20%;
    }
  }
  &__main-img {
    position: absolute;
    left: 45%;
    width: 50%;
    height: 77%;
  }
  &__btn {
    margin-top: 71px;
  }
}
.btn {
  width: 220px;
  height: 56px;
  color: #fff;
  border: none; 
  outline: none;
  cursor: pointer;
  transition-duration: .3s;
  font-size: 18px;
  background: #5A98D0;
  box-shadow: 5px 10px 20px rgba(53, 110, 173, 0.2);
  border-radius: 10px;
  font-size: 16px;
  line-height: 26px;
  &:hover {
    transform: scale(1.1);
    box-shadow: 5px 10px 20px rgba(53, 110, 173, 0.3);

  }
}
.what-we-do {
  padding-top: 180px;
  padding-bottom: 122px;
  &__container {
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__h2 {
    display: block;
    width: 355px;
  }
  &__card {
    margin-left: 90px;
    margin-bottom: 90px;
  }
  article:nth-of-type(2){
        margin-top: -32%;
      }
}
.card {
  max-width: 283px;
  padding: 48px;
  box-shadow: 5px 20px 50px rgba(16,112,177,.2);
  border-radius: 10px;
  &__img-block {
    position: relative;
    &:hover .card__icons {
    
      width: 250px;
      height: 170px;
      top: -60px;
      left: 20px;
      opacity: 1;
    }
  }
  &__icons {
    
    position: absolute;
    width: 0px;
    height: 0px;
    top: 50%;
    left: 50%;
    opacity: 0;
    transition-duration: .5s;
    
  }
  &__image {
    height: 132px;
    margin-bottom: 56px;
    // symbol+use:first-of-type {
    //   display: none;
    // }
    // &:hover symbol+use:first-of-type {
    //   display: block;
    // }
  }
  &__h3 {
    margin-top: 0;
    margin-bottom: 16px;
  }
  p {
    margin-bottom: 32px;
  }
}
.arrow-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 110px;
  height: 30px;
  font-weight: 500;
  font-size: 14px;
  line-height: 30px;
  color: #5A98D0;
  letter-spacing: 0.05em;
  svg {
    transition-duration: .3s;
  }
  &:hover svg {
    transition-duration: .3s;
    transform: translateX(10px);
  }
}

